.container {
    background-color: black;
    width: 80%;
    font-family: monospace;
    color: white;
    align-items: center;
    justify-content: center;
    position: absolute;
}

.text {
    text-decoration: none
}

.intro {
  font-size: 100px;
  display: block; 
  font-family: monospace;
  white-space: nowrap;
  border-right: 4px solid;
  width: 12ch;
 
  animation: typing 2s steps(12),
    blink .5s infinite step-end alternate;
    overflow: hidden;
    
}

@keyframes typing {
    from { width: 0;}
}

@keyframes blink {
    50% { border-color: transparent;}
}

